import { Block, Inline } from '@ali/4ever-cangjie';
import type { HighlightType } from './types';
export const UPDATE_HOVER_HIGHLIGHT = Symbol('UPDATE_HOVER_HIGHLIGHT');
export const REMOVE_HOVER_HIGHLIGHT = Symbol('REMOVE_HOVER_HIGHLIGHT');

export interface IPayload {
  type: HighlightType;
  node: Block | Inline | Array<Block>;
  reason: string;
}

export function updateHighlight(payload: IPayload) {
  return {
    type: UPDATE_HOVER_HIGHLIGHT,
    payload,
  };
}

export function removeHighlight(payload: IPayload) {
  return {
    type: REMOVE_HOVER_HIGHLIGHT,
    payload,
  };
}

export const actions = {
  updateHighlight,
  removeHighlight,
}
